        정하는 바에 의하여 매년 1회 집회되며, 국회의 임시회는 대통령 또는 국회재적의원 4분의 1
        이상의 요구에 의하여 집회된다. 대통령이 임시회의 집회를 요구할 때에는 기간과 집회요구의
        이유를 명시하여야 한다. 훈장등의 영전은 이를 받은 자에게만 효력이 있고, 어떠한 특권도 이에
        따르지 아니한다. 헌법재판소 재판관은 정당에 가입하거나 정치에 관여할 수 없다. 이 헌법시행
        당시에 이 헌법에 의하여 새로 설치될 기관의 권한에 속하는 직무를 행하고 있는 기관은 이 헌법에
        의하여 새로운 기관이 설치될 때까지 존속하며 그 직무를 행한다. 지방자치단체는 주민의 복리에
        관한 사무를 처리하고 재산을 관리하며, 법령의 범위안에서 자치에 관한 규정을 제정할 수 있다.
        군인 또는 군무원이 아닌 국민은 대한민국의 영역안에서는 중대한 군사상
        기밀·초병·초소·유독음식물공급·포로·군용물에 관한 죄중 법률이 정한 경우와 비상계엄이 선포된
        경우를 제외하고는 군사법원의 재판을 받지 아니한다. 누구든지 병역의무의 이행으로 인하여
        불이익한 처우를 받지 아니한다. 정부는 예산에 변경을 가할 필요가 있을 때에는 추가경정예산안을
        편성하여 국회에 제출할 수 있다. 대한민국의 경제질서는 개인과 기업의 경제상의 자유와 창의를
        존중함을 기본으로 한다. 국회는 의장 1인과 부의장 2인을 선출한다. 지방의회의
        조직·권한·의원선거와 지방자치단체의 장의 선임방법 기타 지방자치단체의 조직과 운영에 관한
        사항은 법률로 정한다. 국민경제의 발전을 위한 중요정책의 수립에 관하여 대통령의 자문에 응하기
        위하여 국민경제자문회의를 둘 수 있다. 국회의원의 선거구와 비례대표제 기타 선거에 관한 사항은
        법률로 정한다. 국교는 인정되지 아니하며, 종교와 정치는 분리된다. 국무위원은 국무총리의
        제청으로 대통령이 임명한다. 제3항의 승인을 얻지 못한 때에는 그 처분 또는 명령은 그때부터
        효력을 상실한다. 이 경우 그 명령에 의하여 개정 또는 폐지되었던 법률은 그 명령이 승인을 얻지
        못한 때부터 당연히 효력을 회복한다. 국회는 헌법개정안이 공고된 날로부터 60일 이내에
        의결하여야 하며, 국회의 의결은 재적의원 3분의 2 이상의 찬성을 얻어야 한다. 공무원의 신분과
        정치적 중립성은 법률이 정하는 바에 의하여 보장된다. 비상계엄이 선포된 때에는 법률이 정하는
        바에 의하여 영장제도, 언론·출판·집회·결사의 자유, 정부나 법원의 권한에 관하여 특별한 조치를
        할 수 있다. 각급 선거관리위원회는 선거인명부의 작성등 선거사무와 국민투표사무에 관하여 관계
        행정기관에 필요한 지시를 할 수 있다. 모든 국민은 헌법과 법률이 정한 법관에 의하여 법률에
        의한 재판을 받을 권리를 가진다. 대통령은 제4항과 제5항의 규정에 의하여 확정된 법률을
        지체없이 공포하여야 한다. 제5항에 의하여 법률이 확정된 후 또는 제4항에 의한 확정법률이
        정부에 이송된 후 5일 이내에 대통령이 공포하지 아니할 때에는 국회의장이 이를 공포한다. 국회에
        제출된 법률안 기타의 의안은 회기중에 의결되지 못한 이유로 폐기되지 아니한다. 다만,
        국회의원의 임기가 만료된 때에는 그러하지 아니하다. 농업생산성의 제고와 농지의 합리적인
        이용을 위하거나 불가피한 사정으로 발생하는 농지의 임대차와 위탁경영은 법률이 정하는 바에
        의하여 인정된다. 국가유공자·상이군경 및 전몰군경의 유가족은 법률이 정하는 바에 의하여
        우선적으로 근로의 기회를 부여받는다. 나는 헌법을 준수하고 국가를 보위하며 조국의 평화적
        통일과 국민의 자유와 복리의 증진 및 민족문화의 창달에 노력하여 대통령으로서의 직책을 성실히
        수행할 것을 국민 앞에 엄숙히 선서합니다. 이 헌법시행 당시의 법령과 조약은 이 헌법에 위배되지
        아니하는 한 그 효력을 지속한다. 공개하지 아니한 회의내용의 공표에 관하여는 법률이 정하는
        바에 의한다. 국회는 국민의 보통·평등·직접·비밀선거에 의하여 선출된 국회의원으로 구성한다.
        모든 국민은 고문을 받지 아니하며, 형사상 자기에게 불리한 진술을 강요당하지 아니한다. 국정의
        중요한 사항에 관한 대통령의 자문에 응하기 위하여 국가원로로 구성되는 국가원로자문회의를 둘
        수 있다. 대통령은 국가의 안위에 관계되는 중대한 교전상태에 있어서 국가를 보위하기 위하여
        긴급한 조치가 필요하고 국회의 집회가 불가능한 때에 한하여 법률의 효력을 가지는 명령을 발할
        수 있다. 모든 국민은 법률이 정하는 바에 의하여 공무담임권을 가진다. 국가는 주택개발정책등을
        통하여 모든 국민이 쾌적한 주거생활을 할 수 있도록 노력하여야 한다. 대한민국은 국제평화의
        유지에 노력하고 침략적 전쟁을 부인한다. 제1항의 해임건의는 국회재적의원 3분의 1 이상의
        발의에 의하여 국회재적의원 과반수의 찬성이 있어야 한다. 광물 기타 중요한
        지하자원·수산자원·수력과 경제상 이용할 수 있는 자연력은 법률이 정하는 바에 의하여 일정한
        기간 그 채취·개발 또는 이용을 특허할 수 있다. 재판의 전심절차로서 행정심판을 할 수 있다.
        행정심판의 절차는 법률로 정하되, 사법절차가 준용되어야 한다. 국가는 농업 및 어업을
        보호·육성하기 위하여 농·어촌종합개발과 그 지원등 필요한 계획을 수립·시행하여야 한다.
        누구든지 체포 또는 구속의 이유와 변호인의 조력을 받을 권리가 있음을 고지받지 아니하고는 체포
        또는 구속을 당하지 아니한다. 체포 또는 구속을 당한 자의 가족등 법률이 정하는 자에게는 그
        이유와 일시·장소가 지체없이 통지되어야 한다. 대한민국은 통일을 지향하며, 자유민주적
        기본질서에 입각한 평화적 통일 정책을 수립하고 이를 추진한다. 이 헌법중 공무원의 임기 또는
        중임제한에 관한 규정은 이 헌법에 의하여 그 공무원이 최초로 선출 또는 임명된 때로부터
        적용한다. 대통령이 궐위된 때 또는 대통령 당선자가 사망하거나 판결 기타의 사유로 그 자격을
        상실한 때에는 60일 이내에 후임자를 선거한다. 모든 국민은 능력에 따라 균등하게 교육을 받을
        권리를 가진다. 공공필요에 의한 재산권의 수용·사용 또는 제한 및 그에 대한 보상은 법률로써
        하되, 정당한 보상을 지급하여야 한다. 사면·감형 및 복권에 관한 사항은 법률로 정한다. 재판의
        심리와 판결은 공개한다. 다만, 심리는 국가의 안전보장 또는 안녕질서를 방해하거나 선량한
        풍속을 해할 염려가 있을 때에는 법원의 결정으로 공개하지 아니할 수 있다. 국무총리는
        국무위원의 해임을 대통령에게 건의할 수 있다. 국방상 또는 국민경제상 긴절한 필요로 인하여
        법률이 정하는 경우를 제외하고는, 사영기업을 국유 또는 공유로 이전하거나 그 경영을 통제 또는
        관리할 수 없다. 감사원의 조직·직무범위·감사위원의 자격·감사대상공무원의 범위 기타 필요한
        사항은 법률로 정한다. 행정각부의 장은 국무위원 중에서 국무총리의 제청으로 대통령이 임명한다.
        대통령의 임기연장 또는 중임변경을 위한 헌법개정은 그 헌법개정 제안 당시의 대통령에 대하여는
        효력이 없다.
      </div></slide-window
